import React from 'react';
import { Panel, List, Cell, Group, ScreenSpinner, Div, FormLayout, File, Button,
	Avatar, PanelHeaderSimple, Tabs, TabsItem, PanelHeaderContext, View, Gallery } from '@vkontakte/vkui';

import { MODAL_REWARD_FOR_UPLOAD } from '../modals/Modal';

import Api from '../core/api/Api';

import {getGroupName} from '../components/DangerMeter';

// Redux
import { connect } from 'react-redux';
import { openModal } from '../redux/actions/modal';

import Icon24Camera from '@vkontakte/icons/dist/24/camera';
import './Dairy.css';

class Dairy extends React.Component {

	constructor(props) {
    super(props);
    this.state = {isLoading: true, contextOpened: false, activeTab: 'common'};
  };

	// Компонент примонтирован
	componentDidMount() {
		this.props.fetchedUser && this.common();

		// Upload
		const input = document.getElementById('upload-input');
		const onSelectFile = () => {

			/*if(!this.state.self.canUpload) {
				alert('На данный момент разрешено загружать не более одной фотографии в день.');
				return;
			}*/

			this.setState({isLoading: true});
			Api.upload('dairy/upload?uid='+this.props.fetchedUser.id, input, (self) => {
				if(self) {

					if(self.addedValue) {
						this.props.onOpenModal(MODAL_REWARD_FOR_UPLOAD, {value: self.addedValue});
					}

					this.setState({isLoading: false, self: self, activeTab: 'self'});
				} else {
					this.setState({isLoading: false});
				}
			});
		};
		input && input.addEventListener('change', onSelectFile, false);
  };

	self(page) {
		page = page || 1;

		this.props.fetchedUser && Api.dairy.self(this.props.fetchedUser.id, page, (self) => {
			this.setState({isLoading: false, self: self});
		});

		this.setState({isLoading: true, contextOpened: false, activeTab: 'self'});
	}

	common(page) {
		page = page || 1;

		Api.dairy.common(this.props.fetchedUser.id, page, (common) => {
			this.setState({isLoading: false, common: common});
		});

		this.setState({isLoading: true, contextOpened: false, activeTab: 'common'});
	}

	like(photo, index, e) {

		const button = e.target.tagName === 'BUTTON' ? e.target : e.target.parentNode;

		// Отмена лайка
		if(photo.liked) {
			Api.dairy.unlike(this.props.fetchedUser.id, photo.id, photo.owner.uid, (result) => {
				if(result) {
					let newCommon = Object.assign({}, this.state.common);
					button.className = 'DairyPhoto__LikeBtn';
					newCommon.photos[index].likes = (photo.likes || 0) - 1;
					newCommon.photos[index].liked = false;
					this.setState({common: newCommon});
				}
			});
		}

		// Лайк
		else {
			Api.dairy.like(this.props.fetchedUser.id, photo.id, photo.owner.uid, (result) => {
				if(result) {
					let newCommon = Object.assign({}, this.state.common);
					button.className = 'DairyPhoto__LikeBtnActive';
					newCommon.photos[index].likes = (photo.likes || 0) + 1;
					newCommon.photos[index].liked = true;
					this.setState({common: newCommon});
				}
			});
		}
	}

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeaderSimple
          left={this.props.fetchedUser && <FormLayout action={Api.getDomain('dairy/upload?uid='+this.props.fetchedUser.id)} id="upload-form" className="UploadFrom">
		        <File before={<Icon24Camera />} size="l" id="upload-input">Добавить</File>
		      </FormLayout>}
          separator={false}
        >
          <Tabs>
						<TabsItem selected={true} onClick={() => this.setState({ contextOpened: !this.state.contextOpened })}>
              {this.state.activeTab === 'common' ? 'Общий' : 'Свой'}
            </TabsItem>
          </Tabs>
        </PanelHeaderSimple>

				<PanelHeaderContext
          opened={this.state.contextOpened}
          onClose={() => { this.setState({ contextOpened: false }) }}
        >
          <List>
            <Cell onClick={() => this.self()}>Свой</Cell>
            <Cell onClick={() => this.common()}>Общий</Cell>
          </List>
        </PanelHeaderContext>

				{!this.state.isLoading && <View id="tabs-view" activePanel={this.state.activeTab} header={false}>
					<Panel id="self">
						{this.state.self && <Group>
				      <List className="DairyList">
								{this.state.self.photos.length === 0 && <Div className="DairyDescription">
									А как вы проводите время в самоизоляции?
									<br /><br />
									Добавляйте ежедневное фото, в свой дневник, чтобы запечатлеть хронологию этих странных дней, а также  получить бонусные очки и доступ к просмотру дневников со всей страны!
									<br /><br />
									Чем интереснее ваше фото окажется для других пользователей, тем больше баллов вам будет начислено.
								</Div>}
								{this.state.self.photos.length > 0 && this.state.self.photos.map((photo, index) => {
		              return (
										<Cell key={index}>
											<div className="DairyPhoto">
												<div className="DairyPhoto__Header">
													{photo.date}
												</div>
												<Gallery>
													<div className="img-wrap">
														<img src={photo.link} alt={index} />
														<button type="button" className="DairyPhoto__LikeBtn" disabled="disabled">
															<div className="DairyPhoto__LikeBtnCounter">x {photo.likes || 0}</div>
														</button>
													</div>
					              </Gallery>
											</div>
										</Cell>
									);
								})}
							</List>

							{this.state.self.pagination.count > 1 && <Cell key="pagination"
								before={this.state.self.pagination.current > 1 ?
									<Button onClick={() => this.self(this.state.self.pagination.current - 1)}>Назад</Button> : false}
								indicator={this.state.self.pagination.current < this.state.self.pagination.count ?
									<Button size="l" onClick={() => this.self(this.state.self.pagination.current + 1)}>Далее</Button> : false}
							/>}

						</Group>}
					</Panel>

					<Panel id="common">
						{this.state.common && <Group>
				      <List className="DairyList">
								{this.state.common.photos.map((photo, index) => {
		              return (
										<Cell key={index}>
											<div className="DairyPhoto">
												<div className="DairyPhoto__Header">
													{photo.owner.first_name + ' ' + photo.owner.last_name}
												</div>
												<Gallery>
													<div className="img-wrap">
						                <img src={photo.link} alt={index} />
														<Avatar src={photo.owner.photo_200} />
														<button type="button" className={photo.liked ? 'DairyPhoto__LikeBtnActive' : 'DairyPhoto__LikeBtn'} onClick={(e) => this.like(photo, index, e)}>
															<div className="DairyPhoto__LikeBtnCounter">x {photo.likes || 0}</div>
														</button>
													</div>
					              </Gallery>
											</div>
										</Cell>
									);
								})}
							</List>

							{this.state.common.pagination.count > 1 && <Cell key="pagination"
								before={this.state.common.pagination.current > 1 ?
									<Button onClick={() => this.common(this.state.common.pagination.current - 1)}>Назад</Button> : false}
								indicator={this.state.common.pagination.current < this.state.common.pagination.count ?
									<Button size="l" onClick={() => this.common(this.state.common.pagination.current + 1)}>Далее</Button> : false}
							/>}

						</Group>}
					</Panel>
				</View>}

				{this.state.isLoading && <ScreenSpinner size='large' />}
			</Panel>
		);
	}
}

const mapStateToProps = (state, ownProps) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
	return {
		// Открывает модальное окно
		onOpenModal: (activeModal, modalParams) =>
			new Promise((resolve) =>
				openModal(activeModal, modalParams).then((action) => {
					dispatch(action);
					resolve();
				})
			)
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Dairy);
