/**
 * Методы для работы дневником
 */

const api = {dairy: {
  /**
   * Получить свой дневник
   * @param {Integer} uid ид. полозователя
   * @param {Integer} page страницы (нумерация с 0)
   * @param {Function} handler обработчик ответа в случае успеха
   */
  self: (uid, page, handler) => {
    return api.request({method: 'dairy/self', params: {uid: uid, page: page}, handler: handler});
  },
  /**
   * Получить общий дневник
   * @param {Integer} uid ид. полозователя
   * @param {Integer} page страницы (нумерация с 0)
   * @param {Function} handler обработчик ответа в случае успеха
   */
  common: (uid, page, handler) => {
    return api.request({method: 'dairy/common', params: {uid: uid, page: page}, handler: handler});
  },
  /**
   * Поставить лайк фотографии дневника
   * @param {Integer} uid ид. кто лайкает
   * @param {Integer} photo ид. фотографии
   * @param {Integer} owner ид. полозователя
   * @param {Function} handler обработчик ответа в случае успеха
   */
  like: (uid, photo, owner, handler) => {
    return api.request({method: 'dairy/like', params: {uid: uid, photo: photo, owner: owner}, handler: handler});
  },
  /**
   * Отменить лайк фотографии дневника
   * @param {Integer} uid ид. кто лайкает
   * @param {Integer} photo ид. фотографии
   * @param {Integer} owner ид. полозователя
   * @param {Function} handler обработчик ответа в случае успеха
   */
  unlike: (uid, photo, owner, handler) => {
    return api.request({method: 'dairy/unlike', params: {uid: uid, photo: photo, owner: owner}, handler: handler});
  }
}};

export default api;
